import React from 'react';

const Header = () => {
  return (
    <div className="loading-spinner">
      <div className="row text-center">
        <span className="glyphicon glyphicon-refresh glyphicon-refresh-animate"></span>
      </div>
    </div>
  );
};

export default Header;


